import React from 'react';
import { Card, Avatar, Badge, Tooltip, Table, message, Button, Modal, Form, Input, Select, Radio, InputNumber, DatePicker,Switch } from 'antd';
import FilterForm from '../../components/filterForm/FilterForm';
import axios from '../../axios/axios';
import moment from 'moment';
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;

const roleSelectList = [
    { id: 1, value: '' },
    { id: 2, value: '' },
    { id: 3, value: '' },
    { id: 4, value: '' },
    { id: 5, value: '' }
]
const interestSelectList = [
    { id: 1, value: '' },
    { id: 2, value: '' },
    { id: 3, value: '' },
    { id: 4, value: '' },
    { id: 5, value: 'K' }
]

export default class User extends React.Component {
    
    state = {
        actionBtndisabled: true,//
        deleteVisible: false,//
        title: '',
        isVisible: false,
        type: ''
    };

    params = {
        page: 1,
        pageSize: 10
    }
    search = (postData) => {
        this.params = {
            ...this.params,
            ...postData
        }
        message.info(`${JSON.stringify(this.params)}`);
        this.getList();
    }
    getList = () => {
        axios.getList(this, '/user/list', this.params);
    }

    onRowClick = (item, index) => {
        this.setState({
            selectedRowKeys: [index],
            selectedItem: item,
            actionBtndisabled: false,
        })
    }
    componentDidMount() {
        this.getList();
    }


    //   
    handleOperator = (type) => {
        let item = this.state.selectedItem;
        if (type === 'add') {
            this.setState(() => {
                return {
                    title: '',
                    isVisible: true,
                    type,
                    userInfo: {}
                }
            })
        } else {
            this.setState({
                title: type === 'update' ? '' : '',
                isVisible: true,
                userInfo: item,
                type
            })
        }
    }

    resetSubmit = () => {
        this.setState({
            isVisible: false,
            selectedRowKeys: [],//
            actionBtndisabled: true,
        })
        this.userForm.props.form.resetFields();
    }

    //    -
    handleSubmit = () => {
        let type = this.state.type;
        let _this = this;
        if (type === 'detail') {
            this.setState({
                isVisible: false,
                selectedRowKeys: [],//
                actionBtndisabled: true,
            })
            return;
        }


        this.userForm.props.form.validateFields((err, values) => {
            values.birthday = moment(values.birthday).unix();
            if (!err) {
                
                axios.ajax({
                    url:type == 'add'?'/user/add':'/user/update',
                    data:{
                        ...values
                    }
                }).then((res)=>{
                    if(res.code === 0){
                        _this.resetSubmit();
                        _this.getList();
                    }
                })
            }
        })
    }
    //
    delete = () => {
        let item = this.state.selectedItem;
        let _this = this;
        Modal.confirm({
            title: '',
            okText: '',
            cancelText: '',
            onOk() {
                axios.ajax({
                    url: '/user/delete',
                    data: {
                        userId: item.id,
                    }
                }).then(res => {
                    if (res.code === 0) {
                        message.success('');
                        _this.setState({
                            selectedRowKeys: [],//
                            actionBtndisabled: true,
                        })
                        _this.getList();
                    } else {
                        message.success(res.msg);

                    }
                })
            }

        })


    }

    render() {
        const filterList = [
            {
                type: 'input',
                label: '',
                field: 'userName',
                placeholder: ''
            },
            {
                type: 'input',
                label: '',
                field: 'address',
                placeholder: ''
            }
        ]
        const columns = [
            {
                title: 'id',
                key: 'id',
                dataIndex: 'id'
            },
            {
                title: '',
                key: 'userName',
                dataIndex: 'userName'
            },
            {
                title: '',
                key: 'userImg',
                dataIndex: 'userImg',
                render: (userImg) => {
                    return userImg ? <Avatar src={userImg} /> : <Avatar style={{ backgroundColor: '#87d068' }} icon="user" />
                }
            },
            {
                title: '',
                key: 'sex',
                dataIndex: 'sex',
                render: (sex) => {
                    if (sex === 1) {
                        return <Badge count='' style={{ backgroundColor: '#1890ff' }} />;
                    }
                    return <Badge count='' style={{ backgroundColor: '#52c41a' }} />;
                }
            },
            {
                title: '',
                key: 'age',
                dataIndex: 'age'
            },
            {
                title: '',
                key: 'role',
                dataIndex: 'role',
                render: (role) => {
                    const roleObj = {
                        1: '',
                        2: '',
                        3: '',
                        4: '',
                        5: '',
                    }
                    return roleObj[role];
                }
            },
            {
                title: '',
                key: 'interest',
                dataIndex: 'interest',
                render: (interest) => {
                    const interestObj = {
                        1: '',
                        2: '',
                        3: '',
                        4: '',
                        5: 'K',
                    }
                    let strArr = [];
                    interest.map((item, index) => {
                        strArr.push(interestObj[item]);
                    })

                    return (<Tooltip title={strArr.length > 2 ? strArr.join(',') : ''}>
                        <span>{strArr.length === 0 ? '' : strArr.length > 2 ? strArr.slice(0, 2).join(',') + '...' : strArr.join(',')}</span>
                    </Tooltip>
                    )

                }
            },
            {
                title: '',
                key: 'isMarried',
                dataIndex: 'isMarried',
                render: (isMarried) => {
                    if (isMarried) {
                        return <Badge status="success" text="" />;
                    }
                    return <Badge status="error" text="" />;
                }
            },
            {
                title: '',
                key: 'birthday',
                dataIndex: 'birthday',
                render: (birthday) => {
                    return moment(birthday * 1000).format("YYYY-MM-DD HH:mm:ss");
                }
            },
            {
                title: '',
                key: 'address',
                dataIndex: 'address',
                render: (address) => {
                    return (<Tooltip title={address.length > 10 ? address : ''}>
                        <span>{address.length > 10 ? address.slice(0, 10) + '...' : address}</span>
                    </Tooltip>
                    )
                }
            },
            {
                title: '',
                key: 'time',
                dataIndex: 'time'
            }
        ]
        // 
        columns.map(item => item.align = "center");
        const selectedRowKeys = this.state.selectedRowKeys;
        const rowSelection = {
            type: 'radio',
            selectedRowKeys,
        }
        let footer = {};
        if (this.state.type === 'detail') {
            footer = { footer: null }
        }
        return (
            <div>
                <Card>
                    <FilterForm
                        filterList={filterList}
                        search={this.search}
                    />
                </Card>
                <Card style={{ marginTop: 20 }}>
                    <Button icon="plus" onClick={() => this.handleOperator('add')}></Button>
                    <Button icon="edit" disabled={this.state.actionBtndisabled} style={{ marginLeft: 10 }} onClick={() => this.handleOperator('update')}></Button>
                    <Button icon="info-circle" disabled={this.state.actionBtndisabled} style={{ marginLeft: 10 }} onClick={() => this.handleOperator('detail')}></Button>
                    <Button icon="delete" type="danger" disabled={this.state.actionBtndisabled} style={{ marginLeft: 10 }} onClick={this.delete}></Button>
                </Card>
                <div className="content-wrap">
                    <Table
                        bordered
                        columns={columns}
                        dataSource={this.state.list}
                        pagination={this.state.pagination}
                        rowSelection={rowSelection}
                        onRow={(item, index) => {
                            return {
                                onClick: () => {
                                    this.onRowClick(item, index);
                                }
                            }
                        }}
                    />
                </div>
                <Modal
                    width={800}
                    title={this.state.title}
                    visible={this.state.isVisible}
                    onOk={this.handleSubmit}
                    onCancel={() => {
                        this.resetSubmit();
                    }}
                    {...footer}

                >
                    <UserForm
                        userInfo={this.state.userInfo}
                        type={this.state.type}
                        wrappedComponentRef={(inset) => this.userForm = inset}
                    />
                </Modal>
            </div>
        )
    }
}


class UserForm extends React.Component {
    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 5 },
            wrapperCol: { span: 16 }
        }
        const userInfo = this.props.userInfo || {};
        const type = this.props.type;

        let getRole = (role) => {
            let name = '';
            roleSelectList.map(item => {
                if (item.id === role) {
                    name = item.value;
                }
            })
            return name;
        }

        let getInterest = (interest) => {
            interest = interest || [];
            let names = [];
            interestSelectList.map((item1) => {
                interest.map((item2) => {
                    if (item1.id === item2) {
                        names.push(item1.value)
                    }
                })
            })
            return names.join(',');
        }

        return (
            <Form layout="horizontal">
                <FormItem label="" {...formItemLayout}>
                    {
                        userInfo && type === "detail" ? userInfo.userName :
                            getFieldDecorator('userName', {
                                initialValue: userInfo.userName,
                                rules: [
                                    {
                                        required: true,
                                        message: ''
                                    },
                                    {
                                        min: 5, max: 12,
                                        message: '5-12'
                                    }, {
                                        pattern: new RegExp('^\\w+$', 'g'),
                                        message: ''
                                    }
                                ]
                            })(
                                <Input placeholder="" />
                            )
                    }
                </FormItem>
                <FormItem label="" {...formItemLayout}>
                    {
                        userInfo && type === "detail" ? userInfo.sex === 1 ? '' : '' :
                            getFieldDecorator('sex', {
                                initialValue: userInfo.sex || 1
                            })(
                                <RadioGroup>
                                    <Radio value={1}></Radio>
                                    <Radio value={2}></Radio>
                                </RadioGroup>
                            )
                    }
                </FormItem>
                <FormItem label="" {...formItemLayout}>
                    {
                        userInfo && type === "detail" ? getRole(userInfo.role) :
                            getFieldDecorator('role', {
                                initialValue: userInfo.role
                            })(
                                <Select>
                                    {
                                        roleSelectList.map((item, index) => {
                                            return <Option key={index} value={item.id}>{item.value}</Option>
                                        })
                                    }
                                </Select>
                            )
                    }
                </FormItem>
                <Form.Item label="" {...formItemLayout}>
                    {
                        userInfo && type === "detail" ? getInterest(userInfo.interest) :
                            getFieldDecorator('interest', {
                                initialValue: userInfo.interest,
                            })(
                                <Select mode="multiple" placeholder="">
                                    {
                                        interestSelectList.map((item, index) => {
                                            return (
                                                <Select.Option key={item.id} value={item.id}>{item.value}</Select.Option>
                                            )
                                        })
                                    }

                                </Select>
                            )
                    }
                </Form.Item>

                <Form.Item label="" {...formItemLayout}>
                    {
                        userInfo && type === "detail" ? userInfo.isMarried ? '':'' :
                        getFieldDecorator('isMarried', {
                            initialValue: userInfo.isMarried || false,
                        })(
                            <Switch checkedChildren="" unCheckedChildren="" />
                        )
                    }
                </Form.Item>

                <FormItem label="" {...formItemLayout}>
                    {
                        userInfo && type === "detail" ? moment(userInfo.birthday * 1000).format('YYYY-MM-DD') :
                            getFieldDecorator('birthday', {
                                initialValue: userInfo.birthday ? moment(userInfo.birthday * 1000) : null
                            })(
                                <DatePicker placeholder="" format="YYYY-MM-DD" />
                            )
                    }
                </FormItem>
            </Form>
        )
    }
}
UserForm = Form.create({})(UserForm);
