import React from 'react';
import { Card, Form, Select, Button, Table, message, Modal, Radio, notification, DatePicker } from 'antd';
import axios from '../../axios/axios';
import Utils from './../../utils/utils';
import moment from 'moment';

import FilterForm from '../../components/filterForm/FilterForm';



const FormItem = Form.Item;
const Option = Select.Option;

export default class Order extends React.Component {
    state = {
        list: [],//
        pagination:()=>{},//
        selectedRowKeys: [],//index [0]
        selectedItem: {},//
        isShowFinishOrderModal: false,//
        orderInfo: {},//
        finishOrderBtnDisabled: true,
        orderDetailBtnDisabled:true,
    }

    params = {
        page: 1,
        pageSize: 10,
    }


    //-
    search = (postData) => {
        this.params = {
            ...this.params,
            ...postData
        }
        message.info(`${JSON.stringify(this.params)}`);
        this.getList();
    }

    componentDidMount() {
        this.getList();
    }

    onRowClick = (item,index) => {
        this.setState({
            selectedRowKeys: [index],
            selectedItem: item,
            finishOrderBtnDisabled: false,
            orderDetailBtnDisabled: false,
        })
    }

    //
    orderDetail = () => {
        let orderId = this.state.selectedItem.id;
        window.open(`/#/common/order/detail/${orderId}`,'_blank')
    }

    // 
    finishOrder = () => {
        let item = this.state.selectedItem;
       
        // if (Object.keys(item).length === 0) {
        //     //constructorstateselectedItem{}
        //     Modal.info({
        //         title: '',
        //         content:''
        //     })
        //     return;
        // }
        this.getOrderInfo(item);
    }

    //
    onFinishOrderSubmit = () => {
        axios.ajax({
            url: '/order/finish',
            method: 'post',
            data: {
                orderId:this.state.selectedItem.id
            }
        }).then((res) => {
            if (res.code === 0) {
                message.success('');
                this.setState({
                    isShowFinishOrderModal:false
                })
                this.getList();
                this.setState({
                    selectedRowKeys: [],//
                    selectedItem: {},//
                    finishOrderBtnDisabled: true,
                    orderDetailBtnDisabled: true,
                    
                })
            }
        })
    }

    //
    getOrderInfo = (item) => {
        axios.ajax({
            url: '/order/finish/info',
            params: {
                orderId:item.id
            }
        }).then((res) => {
            if (res.code === 0) {
                this.setState({
                    isShowFinishOrderModal:true,
                    orderInfo:res.result
                })
            }
        })
    }

    getList = () => {
        axios.getList(this,'/order/list',this.params);
    }

    render() {
        const columns = [
            {
                title: '',
                dataIndex: 'orderSn',
            },
            {
                title: '',
                dataIndex: 'bikeSn',
            },
            {
                title: '',
                dataIndex: 'userName',
            },
            {
                title: '',
                dataIndex: 'mobile',
            },
            {
                title: '',
                dataIndex: 'distance',
                render: (distance) => {
                    return distance/1000 + 'km'
                }
            },
            {
                title: '',
                dataIndex: 'totalTime',
            },
            {
                title: '',
                dataIndex: 'status',
                render: (status) => {
                    return status === 1?'':''
                }
            },
            {
                title: '',
                dataIndex:'startTime',
            },
            {
                title: '',
                dataIndex: 'endTime',
            },
            {
                title: '',
                dataIndex: 'totalFee',
            },
            {
                title: '',
                dataIndex: 'userPay',
            }
        ]
        columns.map(item => item.align = "center");
        const selectedRowKeys = this.state.selectedRowKeys;
        const rowSelection = {
            type: 'radio',
            selectedRowKeys
        }
        const formItemLayout = {
            labelCol:{span:5},
            wrapperCol:{span:19}
        }

        const filterList = [
            {
                type: 'select',
                label: '',
                field: 'city',
                placeholder: '',
                width: 135,
                initialValue: '',
                showSearch: true,
                key: 'name',
                value:'id',
                list: [
                    { id: '', name: '' },
                    { id: 1, name: '' },
                    { id: 2, name: '' },
                    { id: 3, name: '' },
                    { id: 4, name: '' },
                    { id: 5, name: '' }
                ]
            },
            {
                type: 'timeSelect',
                label: '',
                width: 200,
            },
            {
                type: 'select',
                label: '',
                field: 'orderStatus',
                placeholder: '',
                width: 135,
                key: 'name',
                value:'id',
                list: [
                    { id: '', name: '' },
                    { id: 1, name: '' },
                    { id: 2, name: '()' },
                    { id: 3, name: '' }
                ]
            },
        ]

        return (
            <div>
                <Card>
                    <FilterForm
                        filterList={filterList}
                        search={this.search} />
                </Card>
                <Card style={{marginTop:20}}>
                    <Button type="primary" disabled={this.state.orderDetailBtnDisabled} onClick={this.orderDetail}></Button>
                    <Button type="primary" disabled={this.state.finishOrderBtnDisabled} style={{marginLeft:10}} onClick={this.finishOrder}></Button>
                </Card>
                <div className="content-wrap">
                    <Table
                        bordered
                        columns={columns}
                        dataSource={this.state.list}
                        pagination={this.state.pagination}
                        rowSelection={rowSelection}
                        onRow={(item,index) => {
                            return {
                                onClick: () => {
                                    this.onRowClick(item,index);
                                }
                            }
                        }}
                    />
                </div>
                <Modal
                    title=""
                    width={600}
                    visible={this.state.isShowFinishOrderModal}
                    onCancel={() => {
                        this.setState({
                            isShowFinishOrderModal:false,
                        })
                    }}
                    onOk={this.onFinishOrderSubmit}
                >
                    <Form layout="horizontal">
                        <FormItem label="" {...formItemLayout}>
                            {this.state.orderInfo.bikeSn}
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                            {this.state.orderInfo.battery + '%'}
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                            {this.state.orderInfo.startTime}
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                            {this.state.orderInfo.location}
                        </FormItem>
                    </Form>
                </Modal>
            </div>
        )
    }
}


